@use "00-base/configure" as *;

.ma__footer-links {

  @include clearfix;

  &__items {

    @include ma-reset-list;
    margin-left: auto;
    margin-right: auto;
    max-width: 480px;

    @media ($bp-small-max) {
      border-bottom: 1px solid;
      margin-bottom: 20px;
      padding-bottom: 20px;
      text-align: center;
    }

    @media ($bp-small-min) {

      @include span-columns(3 of 9);
    }

    @media ($bp-x-large-min) {

      @include span-columns(2 of 9);

      @include shift(1 of 9);
    }
  }

  &__item {
    margin-bottom: 1.125em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {

    @include ma-link-underline;
    padding-bottom: 3px;
  }
}

//theme

.ma__footer-links {

  &__items {

    @media ($bp-small-max) {
      border-bottom-color: $c-bd-divider;
    }
  }

  &__link {
    color: $c-font-base;
    font-weight: $fonts-normal;

    &:hover {
      border-bottom-color: rgba($c-font-dark,.5);
    }
  }
}
